.how {
  margin: 0;
  padding: 1px 20px 30px 20px;

  @media (min-width: @tablet){
  margin: 0;
  padding: 0 20px 102px;
}
}

.how__title {
  margin: 0 0;
  padding: 0;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  width: 230px;
  margin-bottom: 40px;

  @media (min-width: @tablet) {
    font-size: 60px;
    font-weight: 400;
    line-height: 60px;
    width: 375px;
    padding: 17px 0 0px 13px;
  }
}

.how__list {
  margin: 0;
  padding: 0;

  @media (min-width: @tablet) {
    margin: 0;
    padding: 0;
    display: grid;
    grid-template-columns: 280px 280px;
    gap: 103px;
  }

}

.how__item {
  margin: 0;
  padding: 0;
  list-style: none;
  margin-bottom: 20px;
  background-repeat: no-repeat;
  min-height: 60px;
  position: relative;

  @media (min-width: @tablet) {
    min-height: 80px;
    margin: 31px 0 0 13px;
  }
}

.how__item--leaf {

  &::before {
    content: "";
    width: 60px;
    height: 60px;
    position: absolute;
    background: url("../img/leaf.svg") center #68b738 no-repeat;


    @media (min-width: @tablet) {
      width: 80px;
      height: 80px;
    }
  }

  @media (min-width: @tablet) {
    &::after {
      content: "1";
      position: absolute;
      color: #f2f2f2;
      font-size: 280px;
      bottom: -122px;
      left: 119px;
      z-index: -1;
    }
  }
}

.how__description {
  margin: 0 5px 0 79px;
  padding: 4px 0 0;
  color: #444444;
  font-family: @lato;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-left: 79px;

  @media (min-width: @tablet) {
    padding: 111px 0 0 0;
    margin: 0 0 0 2px;
    width: 242px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    position: relative;
  }
}

.how__item--powder {

  &::before {
    content: "";
    background: url("../img/powder.svg") center #68b738 no-repeat;
    width: 60px;
    height: 60px;
    position: absolute;


    @media (min-width: @tablet) {
      width: 80px;
      height: 80px;
    }
  }

  @media (min-width: @tablet) {
    &::after {
      content: "2";
      position: absolute;
      color: #f2f2f2;
      font-size: 280px;
      bottom: -124px;
      left: 112px;
      z-index: -1;
    }
  }
}

.how__item-description {
  margin: 0;
  padding: 2px 0 4px;
  color: #444444;
  font-family: "Lato";
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  margin-left: 79px;
  width: 227px;
}

.how__item--eat {

  &::before {
    content: "";
    background: url("../img/knife.svg") center #68b738 no-repeat;
    width: 60px;
    height: 60px;
    position: absolute;

    @media (min-width: @tablet) {
      width: 80px;
      height: 80px;
    }
  }

  @media (min-width: @tablet) {
    &::after {
      content: "3";
      position: absolute;
      color: #f2f2f2;
      font-size: 280px;
      bottom: -124px;
      left: 112px;
      z-index: -1;
    }
  }
}

.how__item--time {

  &::before {
    content: "";
    background: url("../img/watch.svg") center #68b738 no-repeat;
    width: 60px;
    height: 60px;
    position: absolute;


    @media (min-width: @tablet) {
      width: 80px;
      height: 80px;
    }
  }

  @media (min-width: @tablet) {
    &::after {
      content: "4";
      position: absolute;
      color: #f2f2f2;
      font-size: 280px;
      bottom: -124px;
      left: 112px;
      z-index: -1;
    }
  }
}

.how__list-item {
  list-style: none;
}